<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="80"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">
            Estado de tus consultas
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row class="text-center">
        <v-col>
          <table class="table table-main">
            <thead>
            <tr>
              <th>
                <div>Consultas</div>
              </th>
              <th>Estado</th>
              <th>Pendiente</th>
              <th>Historial</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in consultations" :key="item.id">
              <td class="text-left">
                <div style="color: #084068">{{ item.service.name }}</div>
                <div>COD: {{ item.id | padleft }}</div>
              </td>
              <td>{{ item.status.name }}</td>
              <td v-if="item.status_id === 1" @click="goToPayment(item)">
                <img :src="require('../../assets/icons/table-01.svg')" alt="img">
                Completar pago
              </td>
              <td v-if="[4, 5].includes(item.status_id)" @click="goToDetail(item)">
                <img :src="require('../../assets/icons/table-05.svg')" alt="img">
                Realizar Consulta
              </td>
              <td v-if="![1, 4, 5].includes(item.status_id)">
                <!-- prevent empty space -->
              </td>
              <td @click="goToInvoice(item)">
                <img :src="require('../../assets/icons/table-06.svg')" alt="img">
                Resumen
              </td>
            </tr>
            <tr v-if="consultations.length === 0 && !isLoading">
              <td colspan="10">No hay registros.</td>
            </tr>
            <tr v-if="isLoading">
              <td colspan="10">loading...</td>
            </tr>
            </tbody>
          </table>

          <div class="mt-3">
            <v-btn @click="goToServices" color="primary">Consulta un abogado</v-btn>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
import axios from "axios"

export default {
  name: 'RequestIndex',
  data: () => ({
    isLoading: true,
    consultations: [],
  }),
  computed: {
    ...mapGetters(['user', 'getConsultationFinishedId', 'getConsultationPendingPaymentId']),
  },
  mounted() {
    this.loadConsultations()
  },
  methods: {
    loadConsultations() {
      let url = 'consultations'
      axios.get(url).then((response) => {
        this.consultations = response.data.data
      }).catch(() => {
      }).then(() => {
        this.isLoading = false;
      })
    },
    onSearch(input) {
      this.loadOperations(input)
    },
    goToPayment(item) {
      this.$router.push({name: 'RequestPayment', params: {id: item.id}})
    },
    goToFormData(item) {
      this.$router.push({name: 'RequestStart', params: {id: item.id}})
    },
    goToDetail(item) {
      if (item.status_id === 4) {
        this.goToFormData(item)
        return;
      }

      this.$router.push({name: 'RequestDetail', params: {id: item.id}})
    },
    goToInvoice(item) {
      this.$router.push({name: 'RequestInvoice', params: {id: item.id}})
    },
    goToServices() {
      this.$router.push({name: 'MyLawyer'})
    },
  }
}
</script>
<style lang="scss" scoped>
.table-main {
  thead {
    tr {
      th {
        font-size: 0.8rem;
        position: relative;
      }

      th:nth-child(1) {
        border-top-left-radius: 1rem;
        border-bottom-left-radius: 1rem;

        img {
          position: absolute;
          left: calc(100% / 2 - 100px);
          top: -5px;
        }
      }

      th:nth-child(4) {
        border-top-right-radius: 1rem;
        border-bottom-right-radius: 1rem;

        img {
          position: absolute;
          left: 0;
          top: -5px;
        }
      }

      th:nth-child(1) div {
        color: #707578;
        border-radius: 1rem;
        background-color: white;
      }
    }
  }
}
</style>